@font-face {
  font-family: 'FineCollege';
  src: url('./FineCollege.ttf') format('truetype');
}
@font-face {
  font-family: 'CubbyBrush';
  src: url('./CubbyBrush.ttf') format('truetype');
}
@font-face {
  font-family: 'SaucerBB';
  src: url('./SaucerBB.ttf') format('truetype');
}

.calculation {
  width: fit-content;
  margin: 8px;
  display: flex;
  flex-direction: column;
  border: 2px dotted blue;
  padding: 8px;
}

.operator {
  height: 0px;
  color: #0051f5;
  font-size: 100px;
  display: flex;
  align-items: center;
}

.digit {
  min-width: 60px;
  max-width: 60px;
  height: 80px;
  background: #0051f5;
  color: #fae712;
  cursor: move;
  font-size: 100px;
  border: 1px dashed grey;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 8px;
  padding: 12px;
}

$images: (
  'character-0.svg',
  'character-1.svg',
  // 'character-1.svg',
  // 'character-2.svg',
  // 'character-3.svg',
  // 'character-4.svg',
  // 'character-5.png',
  // 'character-6.png',
  // 'character-7.png',
  // 'character-8.jpg',
  // 'character-9.jpg',
  // 'character-10.png',
  // 'character-11.png'
);

.digit-panel {
  min-width: 60px;
  max-width: 60px;
  height: 80px;
  border: 1px dashed red;
  border-radius: 20px;
  margin: 8px;
  padding: 12px;
  // background-image: url('../images/' + nth($images, random(3)));
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.content-panel {
  position: fixed;
  bottom: 0;
  overflow-y: scroll;
  width: 100%;
  height: calc(100% - 124px);
}

.letter,
.letter-panel {
  min-width: 60px;
  max-width: 60px;
  height: 60px;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 8px;
  padding: 12px;

  &.letter {
    color: #fae712;
    cursor: move;
    font-family: FineCollege;
    font-size: 72px;
    background: #0051f5;
    border: 1px dashed grey;
  }

  &.letter-panel {
    border: 1px dashed red;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
  }
}

.word {
  position: fixed;
  margin-top: 300px;
  background-color: transparent;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.word-picture {
  min-width: 240px;
  max-height: 240px;
  margin: 20px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.word-panel {
  display: flex;
}

.button {
  font-weight: bold;
  margin: 16px;
  cursor: pointer;
  height: 96px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
